body {
    margin : 0;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align : center;
    min-height: 100vh;
}
h1 {
    margin: 0;
    margin-bottom: .5rem;
}

.input {
    border: 1px solid #333;
    border-radius: .3em;
    padding: .25rem;
    width: 7em;
}

select {
    margin: 0;
    margin-left: .5rem;
}

.equals {
    font-weight: bold;
    font-size: 2rem;
}